/* ==========================================================================
   & surchage bootstrap button-groups.css
   ========================================================================== */

.btn-group.open {
    .dropdown-toggle {
        box-shadow: none;
    }
}

.btn-default {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active,
    &:active:hover,
    &:active:focus,
    &:active.focus,
    &.active:hover,
    &.active:focus,
    &.active.focus,
    .open > &.dropdown-toggle,
    .open > &.dropdown-toggle:hover,
    .open > &.dropdown-toggle:focus,
    .open > &.dropdown-toggle.focus {
        background-color:$white;
        border-color: $base-color;
        color:inherit;
        outline:0!important;
        .error & {
            border-color:$error-color;
        }
    }
    margin-top:0;
    margin-bottom:0;
}


