/* ==========================================================================
   & surchage bootstrap-select.css
   ========================================================================== */

.bootstrap-select  {
    > .dropdown-toggle {
        position:relative;
        box-shadow: none;
        font-family: $base-font-family;
        font-size: 1.2rem;
        font-weight:normal;
        padding: 0;
        background-color:transparent;
        height:2.2rem;
        &:focus {
            outline: none !important;
        }
        &.btn-default {
            background-color:transparent;
        }
    }
    &.btn-group {
        background-color:transparent;
        height: auto;
        box-shadow: none;
        .dropdown-toggle {
            .filter-option {
                font-size:1.6rem;
                color:$base-color;
                margin-top:3px;
            }
            .caret {
                @include icon(arrow);
                border:0;
                right: 0;
                width:auto;
                height:auto;
                margin-top: -0.4rem;
                font-size:0.8rem;
                color:$greyPurple;
            }
        }
        .dropdown-menu {
            @include boxShadow(none);
            border-radius:0;
            border:1px solid rgba($blue,.1);
            font-size: 1.6rem;
            border-bottom:2px solid $blue;
            padding:0;
            > li {
                > a {
                    padding:0.7rem 1.5rem;
                    &:hover,
                    &:focus {
                        background-color:rgba($blue,.1);
                    }
                }
            }
        }
    }

}
