// ==========================================================================
// iconfont template
// ==========================================================================


@font-face {
    font-family: "iconfont";
    src:
        url('../font/iconfont.woff2') format('woff2'),
        url('../font/iconfont.woff') format('woff'),
        url('../font/iconfont.ttf') format('truetype');
}

@mixin icon-styles {
    font-family: "iconfont";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none;
    text-transform: none;
}

%icon {
    @include icon-styles;
}

@function icon-char($filename) {
    $char: "";

    @if $filename == arrow {
        $char: "\E001";
    }
    @if $filename == check {
        $char: "\E002";
    }
    @if $filename == close {
        $char: "\E003";
    }
    @if $filename == minus {
        $char: "\E004";
    }
    @if $filename == plus {
        $char: "\E005";
    }

    @return $char;
}

@mixin icon($filename, $insert: before, $extend: false) {
    &:#{$insert} {
        @if $extend {
            @extend %icon;
        } @else {
            @include icon-styles;
        }
        content: icon-char($filename);
    }
}

.icon-arrow {
    @include icon(arrow);
}
.icon-check {
    @include icon(check);
}
.icon-close {
    @include icon(close);
}
.icon-minus {
    @include icon(minus);
}
.icon-plus {
    @include icon(plus);
}
