// couleurs
$white : #FFF;
$black : #000;
$red : #D60036;
$green : #398C47;

$blue : #003EA9;
$blueDark : #1D2A59;
$blueActive : #1869D6;

$greyPurple : #6F7099;
$grey : #C9C9CE;
$greyActive : #A6A6AC;

$base-color : $blueDark;
$error-color : $red;
$success-color : $green;

// typographie
$base-font-size : 1.4rem;
$base-line-height : 1.3;


$font-family-1 : 'OpenSans', Arial, sans-serif;

$base-font-family : $font-family-1;
$alpha-font-family : $base-font-family;

// margin
$base-margin : 2rem;
