/* ==========================================================================
   & forms
   ========================================================================== */

.form {
    padding:1.5rem 2rem 2rem 2rem;
}
.field {
    position:relative;
    border-bottom:2px solid rgba($blue,.1);
    margin-bottom:0;
    padding-top:1.5rem;
    padding-bottom:.5rem;
    transition: .3s ease all;
    & + & {
        margin-top:2rem;
    }
    &__label {
        color:$greyPurple;
        font-size:1.6rem;
        font-weight:400;
        position: absolute;
        z-index:3;
        background-color:$white;
        left:0;
        top: 1.5rem;
        margin-bottom:0;
        transition: .3s ease all;
        &--textarea {
            top:auto;
            bottom: 0.5rem;
        }
    }
    &__control {
        box-shadow:none;
        font-size:1.6rem;
        color:$base-color;
        border-radius:0;
        padding:0;
        height:2.2rem;
        &[type="password"]{
            color:$blue;
        }
        &[readonly]{
            color:$grey;
            background-color:transparent;
        }
        &--countrycode {
            width:4rem;
        }
        &--flex {
            flex-grow:1;
            width:auto;
            min-width: 0;
        }
    }
    &__row {
        display:flex;
        align-items: flex-end;
    }
    &__link {
        flex-shrink: 0;
        margin-left: 1rem;
        margin-bottom: 2px;
        font-weight:600;
    }
    &--focus {
        border-color:$blue;
    }
    &--focus,
    &--not-empty {
        .field__label {
            font-size:1.2rem;
            font-weight:600;
            top:0;
            &--textarea {
                top:auto;
                bottom:calc(100% - 1rem);
            }
        }
        .field__control {
            box-shadow:none;
        }
    }
    &.error {
        color: $error-color;
        border-color: $error-color;
    }
    label.error {
        display:block;
        font-size:1rem;
        position:absolute;
        bottom:-2.3rem;
        left:0;
        &#input-mobile-phone-error{
            bottom: -2.8rem;
            left: -3.2rem;
        }
    }
    &__textarea {
        &.form-control {
            height:6rem;
        }

    }
}

.field-checkbox {
    margin-bottom:0;
}


// custom checkbox & radio
.custom-checkbox,
.custom-radio {
    display: none;
    + label {
        position: relative;
        padding-left: 3rem;
        vertical-align: top;
        cursor:pointer;
        display:inline-block;
        font-size:1.2rem;
        &:before,
        &:after {
            display: block;
            position: absolute;
        }
        &:before {
            content: "";
            cursor: pointer;
            top: 0;
            left: 0;
            height: 1.6rem;
            width: 1.6rem;
            border:2px solid $blue;

        }
    }
}

// checkbox
.custom-checkbox {
    + label {
        @include icon(check, after);
        font-weight: 400;
        &:after {
            top: 0;
            left: 0;
            font-size: 1rem;
            color: transparent;
            width: 1.6rem;
            height: 1.6rem;
            line-height: 1.6rem;
            text-align:center;
        }
    }
    &:checked {
        + label {
            &:after {
                color: $white;
                background-color: $blue;
            }
        }
    }
}

// radio
.custom-radio {
    + label {
        &:before {
            border-radius:50%;
        }
        &:after {
            content: '';
            top: 5px;
            left: 5px;
            height: 0.6rem;
            width: 0.6rem;
            line-height: 0.6rem;
            border-radius: 50%;
        }
    }
    &:checked {
        + label {
            &:before {
                background-color: $blue;
            }
            &:after {
                background-color: $white;
            }
        }
    }
}




