/* ==========================================================================
   & main
   ========================================================================== */

.layout-1 {
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
}

.layout-2 {
    height: 100vh;
}

.main {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    &--layout-2 {
        height:100%;
        justify-content: space-between;
    }
    &--full-height {
        height: 100%;
        justify-content: space-between;
    }
}

.main-top {
    @media screen and (orientation: portrait){
        overflow: auto;
        -webkit-overflow-scrolling:touch;
        height:100%;
    }
}

.main-bottom {

}

.row {
    & + & {
        @include mar-t;
    }
}
