/* ==========================================================================
   & header
   ========================================================================== */

.header {
    @include cover;
    position: relative;
    flex-grow: 1;
    overflow:hidden;
    min-height:20rem;
    &__logo {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

    }
}
