/* ==========================================================================
   & typography
   ========================================================================== */


$fontPath : '../font/';

@mixin fontFace($name, $family, $weight : 400, $style : normal){
  @font-face {
    font-family: $family;
    src:  url('#{$fontPath}#{$name}.woff2') format('woff2'),
          url('#{$fontPath}#{$name}.woff') format('woff'),
          url('#{$fontPath}#{$name}.ttf') format('truetype');
    font-weight: $weight;
    font-style: $style;
  }
}
@include fontFace('OpenSans-Semibold', 'OpenSans', 600);
@include fontFace('OpenSans-Regular', 'OpenSans', 400);
@include fontFace('OpenSans-Light', 'OpenSans', 300);
