/* ==========================================================================
   & buttons
   ========================================================================== */

.btn {
    @include withHoverFocus {
        color:$white;
    }
    display:block;
    width:100%;
    vertical-align: middle;
    border:none;
    line-height: 1;
    position: relative;
    border-radius:0;
    white-space: normal;
    &:focus {
        &,
        &:active {
            outline: 0;
        }
    }
    &:active {
        box-shadow:none;
    }
}

.btn-alpha {
    text-transform: uppercase;
    background-color:$grey;
    font-size:1.6rem;
    font-weight:600;
    padding:2.2rem 1rem;
    letter-spacing: 1px;
    &:active,
    &.active {
        background-color:$greyActive;
    }
    &--primary {
        background-color:$blue;
        &:active,
        &.active {
            background-color:$blueActive;
        }
    }
}
