/* ==========================================================================
   & common
   ========================================================================== */


.title {
    text-align:center;
    &__bold {
        font-weight:600;
    }
}

.link {
    text-decoration:underline;
    font-weight:400;
}


.alert {
    background-color:$grey;
    color:$white;
    &--success {
        background-color:$success-color;
    }
    &--error {
        background-color:$error-color;
    }
    &--hide {
        display:none;
    }
    &__inner {
        display:table;
        width:100%;
        padding:1.5rem;
    }
    &__text {
        display:table-cell;
        font-size:1.2rem;
    }
    &__btn {
        display:table-cell;
        padding-left:1rem;
        vertical-align: middle;
        text-align: right;
    }
}

.message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align:center;
    flex-grow:1;
    padding:2rem;
    color:$blueDark;
    &__icon {
        &:before {
            display:block;
            margin-bottom:6rem;
        }
        &--valid {
            @include icon(check);
            &:before {
                font-size:5.5rem;
            }
        }
        &--error {
            @include icon(close);
            &:before {
                font-size:6.5rem;
            }
        }
    }
    &__title {
        @include mar-b(4rem);
        font-size:2rem;
    }
    &__subtitle {
        font-weight:300;
        font-size:1.6rem;
    }
}

.faq {
    padding:2rem 2rem 0 2rem;
    &__title {
        @include mar-b;
        text-align:center;
        font-size:2rem;
    }
    &__subtitle {
        @include mar-b;
        text-align:center;
        font-size:1.6rem;
        font-weight:300;
    }
    &__list {
        @include mar-h(-2rem);
        border-bottom:1px solid $grey;
    }
}

.question {
    border-top:1px solid $grey;
    &__link {
        margin:2rem;
        display:block;
        position:relative;
    }
    &__title {
        font-size:1.6rem;
    }
    &__toggle-icon {
        @include icon(minus);
        position: absolute;
        top: 50%;
        right:0;
        margin-top:-1rem;
        .js-toggle--hide & {
            @include icon(plus);
        }
    }
    &__text {
        @include pad-h;
        @include mar-b;
    }
    &__btn {
        @include mar-b;
    }
}
