// ==========================================================================
// mixins
// ==========================================================================


// Margin
@mixin mar-t ($default : $base-margin) {
    margin-top: $default;
}
@mixin mar-r ($default : $base-margin) {
    margin-right: $default;
}
@mixin mar-b ($default : $base-margin) {
    margin-bottom: $default;
}
@mixin mar-l ($default : $base-margin) {
    margin-left: $default;
}
@mixin mar-v ( $top : $base-margin, $bottom : $top) {
    margin-top: $top;
    margin-bottom: $bottom;
}
@mixin mar-h ( $left : $base-margin, $right : $left) {
    margin-left: $left;
    margin-right: $right;
}


// Padding
@mixin pad-t ($default : $base-margin) {
    padding-top: $default;
}
@mixin pad-r ($default : $base-margin) {
    padding-right: $default;
}
@mixin pad-b ($default : $base-margin) {
    padding-bottom: $default;
}
@mixin pad-l ($default : $base-margin) {
    padding-left: $default;
}
@mixin pad-v ( $top : $base-margin, $bottom : $top) {
    padding-top: $top;
    padding-bottom: $bottom;
}
@mixin pad-h ( $left : $base-margin, $right : $left) {
    padding-left: $left;
    padding-right: $right;
}

// FontFormat
@mixin fontFormat ( $family : $alpha-font-family, $size : 1.4rem, $weight: normal, $transform : none, $color : inherit) {
    font-family: $family;
    font-size: $size;
    font-weight: $weight;
    text-transform: $transform;
    color: $color;
}


// Title
@mixin fontTitle ($size : 2rem, $color : inherit, $weight: bold, $transform : uppercase, $line-height:1.1, $family : $alpha-font-family) {
    @include fontFormat ( $family, $size, $weight, $transform, $color );
    line-height: $line-height;
}

@mixin titleOne ($size : 1.5rem, $color : inherit, $decoBottom : 1rem ) {
    @include fontTitle( $size : $size, $color : $color );
    @include decoTitle( $bottom : $decoBottom );
}

@mixin decoTitle ($bottom : 0.8rem) {
    &:before {
        content:"";
        display:block;
        width:8.3rem;
        height:0.7rem;
        background-color:$red;
        margin-bottom:$bottom;
    }
}

// Text
@mixin fontText ($size : 1.4rem, $color : inherit, $transform : none, $weight: normal, $family : $alpha-font-family ) {
    @include fontFormat ( $family, $size, $weight, $transform, $color );
}


// cover
@mixin cover( $bg-position-x : 50%, $bg-position-y : 50%) {
    background-size: cover;
    background-position: $bg-position-x $bg-position-y;
}

// contain
@mixin contain( $bg-position-x : 50%, $bg-position-y : 50%) {
    background-size: contain;
    background-position: $bg-position-x $bg-position-y;
}

// boxShadow
@mixin boxShadow ($default : 2px 2px 5px 0px rgba(0, 0, 0, 0.3))  {
    box-shadow: $default;
}

// placeholder
@mixin placeholder {
    &::-webkit-input-placeholder { @content; }
    &::-moz-placeholder { @content; }
    &:-ms-input-placeholder { @content; }
    &:-moz-placeholder { @content; }
}

// customScrollbar
@mixin customScrollbar ($color : $white, $bgColor : #6b6565, $width : 0.4rem, $marginLeft : 1rem){
    &::-webkit-scrollbar {
        width:$width;
        background-color: $bgColor;
        $mar-l: 1rem;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $color;
    }
}

// flexWidth
@mixin flexWidth($width : 50%, $gutter : 2rem) {
    width:calc(#{$width} - #{$gutter} - .01px);
}

// flexCenter
@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

// centerIcon
@mixin centerIcon($parentPosition : relative, $pseudo : before) {
    position:$parentPosition;
    &:#{$pseudo} {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
}

// withHoverFocus
@mixin withHoverFocus {
    &,
    &:hover,
    &:focus {
        @content;
    }
}


// colorHoverFocus
@mixin colorHoverFocus($color:inherit) {
    &,
    &:hover,
    &:focus {
        color:$color;
    }
}



//============================================================
//
// CSS arrow
//
// @param direction       :  up, down, left, right
// @param width           :  px, em
// @param height          :  px, em
// @param color           :  hex, rgb
//
//============================================================

@mixin arrow($direction: up, $width: 1rem, $height: 1rem, $color: null) {
    width: 0;
    height: 0;

    // Right
    @if $direction == right {
        border-top: $width/2 solid transparent;
        border-bottom: $width/2 solid transparent;
         @if $color == null {
            border-left: $height solid;
        } @else {
            border-left: $height solid $color;
        }
    }
    // Left
    @if $direction == left {
        border-top: $width/2 solid transparent;
        border-bottom: $width/2 solid transparent;
        @if $color == null {
            border-right: $height solid;
        } @else {
            border-right: $height solid $color;
        }
    }
    // Up
    @if $direction == up {
        border-left: $width/2 solid transparent;
        border-right: $width/2 solid transparent;
        @if $color == null {
            border-top: $height solid;
        } @else {
            border-top: $height solid $color;
        }
    }
    // Down
    @if $direction == down {
        border-left: $width/2 solid transparent;
        border-right: $width/2 solid transparent;
        @if $color == null {
            border-top: $height solid;
        } @else {
            border-top: $height solid $color;
        }
    }
}
