/* ==========================================================================
   & footer
   ========================================================================== */

.footer {
    text-align:center;
    position: relative;
    @include pad-v(2.1rem, 2.7rem);
    &__list {

    }
    &__item {
        display:inline-block;
        & + & {
            &:before {
                content:"";
                display:inline-block;
                width:1px;
                height:1rem;
                background-color:$base-color;
            }
        }
    }
    &__link {
        @include pad-h(1rem);
        font-size:1.2rem;
    }
    &:before {
        content:"";
        display:block;
        min-width:100%;
        height:6px;
        position:absolute;
        bottom:0;
        background: linear-gradient(to right, #1869D6 0%, #003EA9 100%);
    }
}

.footer-wrapper {
    position:fixed;
    width:100%;
    bottom:0;
}
